@import url('https://fonts.googleapis.com/css?family=Roboto');

body {
  height: 100%;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5em;
  color: rgba(0, 0, 0, 0.87);
  background: #f5f5f5;
}

#root {
  position: fixed;
  top: 50%;
  left: 50%;
  display: flex;
  flex-direction: column;
  transform: translate(-50%, -50%);
}
